const useTreeLayout = (data) => {
    return useMemo(() => {
        if (!data) return { nodes: [], links: [] };
        const root = d3.hierarchy(data, d => d.children);
        // Adjust the size for better spacing. The dimensions are swapped for a horizontal layout.
        const treeLayout = d3.tree().size([800, 500]);
        const tree = treeLayout(root);
        
        let nodes = tree.descendants();
        // Swap x and y for horizontal layout and add some padding
        nodes.forEach(node => {
            [node.x, node.y] = [node.y + 30, node.x];
        });

        const links = tree.links();
        return { nodes, links };
    }, [data]);
};

// --- React Components ---
const Node = ({ node, style, onClick }) => {
    const isLeaf = !node.data.children;
    const isClickable = isLeaf || node.data.url;

    const handleClick = () => {
        if (node.data.url) {
            window.open(node.data.url, '_blank');
        } else {
            onClick();
        }
    };

    return (
        <animated.g style={style} transform={`translate(${node.x},${node.y})`} onClick={handleClick}>
            <circle 
                r={isLeaf ? 6 : 8} 
                className={isClickable ? 'node-circle clickable' : 'node-circle'}
            />
            <text
                dy="0.32em"
                x={node.children ? -12 : 12}
                textAnchor={node.children ? "end" : "start"}
                className="node-text"
            >
                {node.data.name}
            </text>
        </animated.g>
    );
};

const Link = ({ link, style }) => {
    // d3.linkHorizontal() generates the path data for a smooth curve
    const linkGenerator = d3.linkHorizontal().source(d => [d.source.x + 8, d.source.y]).target(d => [d.target.x - 8, d.target.y]);
    return <animated.path style={style} className="link" d={linkGenerator(link)} />;
};

const InfoPanel = ({ node, onClose }) => {
    if (!node) return null;

    return (
        <div className="info-panel">
            <button onClick={onClose} className="close-btn">&times;</button>
            <h3>{node.name}</h3>
            {node.description && <p>{node.description}</p>}
            {node.url && (
                <a href={node.url} target="_blank" rel="noopener noreferrer">
                    View Link
                </a>
            )}
        </div>
    );
};


const Tree = ({ data, onNodeSelect }) => {
    const [treeData, setTreeData] = useState(data);

    // This function toggles the children of a node on click
    const handleNodeClick = useCallback((clickedNode) => {
        // If it's a leaf node, show its info
        if (!clickedNode.data.children) {
            onNodeSelect(clickedNode.data);
            return;
        }

        // Otherwise, expand/collapse it
        setTreeData(currentData => {
            const newTreeData = JSON.parse(JSON.stringify(currentData)); // Deep clone
            const findAndToggle = (node, id) => {
                if (node.id === id) {
                    if (node.children) {
                        node._children = node.children;
                        delete node.children;
                    } else {
                        node.children = node._children;
                        delete node._children;
                    }
                    return true;
                }
                if (node.children) {
                    for (const child of node.children) {
                        if (findAndToggle(child, id)) return true;
                    }
                }
                 if (node._children) {
                    for (const child of node._children) {
                        if (findAndToggle(child, id)) return true;
                    }
                }
                return false;
            };
            
            // Add unique IDs to nodes if they don't have them
            let idCounter = 0;
            const addIds = (node) => {
                node.id = node.id || `node-${idCounter++}`;
                if(node.children) node.children.forEach(addIds);
                if(node._children) node._children.forEach(addIds);
            }
            addIds(newTreeData);
            clickedNode.data.id = clickedNode.data.id || `node-${idCounter++}`;


            findAndToggle(newTreeData, clickedNode.data.id);
            return newTreeData;
        });
    }, [onNodeSelect]);

    const { nodes, links } = useTreeLayout(treeData);

    const nodeTransitions = useTransition(nodes, {
        key: d => d.data.name,
        from: { opacity: 0, transform: 'scale(0.8)' },
        enter: { opacity: 1, transform: 'scale(1)' },
        leave: { opacity: 0, transform: 'scale(0.8)' },
        config: { tension: 210, friction: 20 },
    });

    const linkTransitions = useTransition(links, {
        key: d => `${d.source.data.name}-${d.target.data.name}`,
        from: { opacity: 0 },
        enter: { opacity: 1 },
        leave: { opacity: 0 },
        config: { tension: 210, friction: 20 },
    });


    return (
        <svg width="100%" height="100%" viewBox="0 0 700 850">
            <g>
                {linkTransitions((style, link) => (
                    <Link link={link} style={style} />
                ))}
                {nodeTransitions((style, node) => (
                    <Node node={node} style={style} onClick={() => handleNodeClick(node)} />
                ))}
            </g>
        </svg>
    );
};


function App() {
    const [selectedNode, setSelectedNode] = useState(null);

    useEffect(() => {
        // Set initial selected node to the root's first child's first child (Bio)
        const initialNode = portfolioData?.children?.[0]?.children?.[0];
        if (initialNode) {
            setSelectedNode(initialNode);
        }
    }, []);

    const handleClosePanel = () => {
        setSelectedNode(null);
    };

    return (
        <div className="app-container">
            <div className="tree-container">
                <Tree data={portfolioData} onNodeSelect={setSelectedNode} />
            </div>
            <InfoPanel node={selectedNode} onClose={handleClosePanel} />
        </div>
    );
}

const container = document.getElementById('root');
const root = createRoot(container);
root.render(<App />);

export default App;
